
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap');


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .fa {
    animation: fa infinite 3s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.fa {
  color: darkolivegreen;
  margin: 0px 5px
}

@keyframes fa {
  from {
    transform:rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
.card{

  height: 400px;
  background:rgba(26, 156, 216, 0.6);
  width: 20rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4);
  border: none;
} 
.search-box{
  border-radius: 70px;
  padding: 10px 0px;
  /* width: 60%; */
  font-family: 'Roboto', sans-serif;
  text-align: center;
  font-size: 20px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
}

